<template>
    <div class="row mb-3">
        <div class="col">
            <h5>{{id}}</h5>
            <hr />
            <div class="log">
                <p v-for="line in reversed" :key="line">{{line}}</p>
            </div>
            <hr />
        </div>
    </div>
</template>

<script setup>
import { computed } from "vue";
const props = defineProps({
    id: String,
    data: Object
});
const reversed = computed(() => {
    var logs = props.data;
    return logs.reverse();
});
</script>

<style scoped>
.log {
    display: flex;
    flex-direction: column;
    max-height: 300px;
    overflow-y: scroll;
    mask-image: linear-gradient(to bottom, black 75%, transparent 100%);
    padding-bottom: 40px;
}
